import React, { useEffect, useState } from "react";
import MyPageContent from "../../styles/mypage/MyPage";
import { SERVER_URL } from "../../api/config";
import jwtAxios from "../../util/jwtUtil";
import { buttonPrimaryStyle } from "../../styles/sign/signArea";
import Address from "../../components/singup/Address";
import { Button, Form, Input, Select } from "antd";
import { Common } from "../../styles/CommonCss";
import axios from "axios";
import { Link, useNavigate } from "react-router-dom";
import Swal from 'sweetalert2/dist/sweetalert2.js'
import 'sweetalert2/src/sweetalert2.scss'

const columns = [
  {
    title: "",
    dataIndex: "name",
    render: () => (
      <img style={{ width: "80px" }} src="/images/moon.jpg" alt=" " />
    ),
  },
  {
    title: " | ",
    dataIndex: "test",
    render: () => (
      <div>
        <p> </p>
        <p>12121212-1212121</p>
      </div>
    ),
  },
  {
    title: "",
    dataIndex: "date",
  },
  {
    title: "",
    dataIndex: "math",
  },
  {
    title: "",
    dataIndex: "order",
  },
  {
    title: "",
    button: <button>ddldldd</button>,
    render: () => <button> </button>,
  },
];

const MyMainPage = () => {
  const [email, setEmail] = useState();
  const [phone, setPhone] = useState();
  const [nickname, setNickname] = useState();

  const [address, setAddress] = useState("");
  const [address2, setAddress2] = useState("");

  const navigate = useNavigate();

  useEffect(()=>{
    const fetchData = async()=>{
      jwtAxios.get(`${SERVER_URL}/user/info`)
      .then(result=>{
        console.log(result);
        setEmail(result.data.email);
        setPhone(result.data.phone);
        setNickname(result.data.nickname);
        setAddress(result.data.address);
        setAddress2(result.data.address2);
      })
      .catch(e=>{
        console.log(e);
        Swal.fire(
          // {
          //   title: "",
          //   text: "<p style='font-size:5rem;margin:1rem;'>  .</p>",
          //   icon: "info"
          // }
          {
          title:"<p style='font-size:4rem;margin:1rem;'></p>",
          icon: "info",
          width: 600,
          confirmButtonText: `<span style="display:bolck;font-size:4rem;width:200px;padding:1rem;"></span>`,
          confirmButtonColor: `${Common.color.f900}`,
        }).then(function(){
          navigate('/sign/in');
        });
      });
    }
    fetchData();
  },[]);

  const doSubmit = () => {
    jwtAxios.put(`${SERVER_URL}/user/updateUser`, {
      "nickname": nickname,
      "phone": phone,
      "address": address,
      "address2": address2
    }).then(data => {
      console.log(data);
      Swal.fire(
        {
        title:"<p style='font-size:4rem;margin:1rem;'>  .</p>",
        icon: "info",
        width: 600,
        confirmButtonText: `<span style="display:bolck;font-size:4rem;width:200px;padding:1rem;"></span>`,
        confirmButtonColor: `${Common.color.f900}`,
      }
    );
    }).catch(e => {
      console.log(e);
    });
  }

  const updateAddressInfo = ({ zonecode, address }) => {
    //   
    // setZonecode(zonecode);
    setAddress(address);
  };

  return (address) &&(
    <div style={{ width: '100%' }}>
      <Form name="mypage_update">
        <MyPageContent> </MyPageContent>
        <hr />
        <div style={{ margin: '2rem', width: '100%' }}>
          <h2 style={{ padding: '0.7rem', fontSize: '2rem' }}> </h2>
          <div style={{ display: 'flex', flexDirection: 'column' }}>
            <div style={{ width: '100%' }}>
              <Form.Item>
                <Input placeholder='email'
                  disabled
                  style={{
                    width: '40%', fontSize: '2rem',
                    padding: '1rem', margin: '0.5rem'
                  }} value={email} onChange={(e) => setEmail(e.target.value)} />
              </Form.Item>
              <Form.Item>
                <Input placeholder='phone'
                  style={{
                    width: '40%', fontSize: '2rem',
                    padding: '1rem', margin: '0.5rem'
                  }}
                  value={phone}
                  onChange={(e) => setPhone(e.target.value)} />
              </Form.Item>
            </div>
            <div style={{ flexGrow: 1 }}>
              <Form.Item>
                <Input placeholder='nickname'
                  style={{
                    width: '40%', fontSize: '2rem',
                    padding: '1rem', margin: '0.5rem'
                  }}
                  value={nickname}
                  onChange={(e) => setNickname(e.target.value)} />
              </Form.Item>
            </div>
          </div>
        </div>
        <hr style={{ marginBottom: "2rem" }}></hr>
        <div style={{ padding: "2rem" }}>
          <Address onAddressChange={updateAddressInfo} address={address} />
          <Form.Item>
            <Input
              style={{ height: 60, fontSize: "20px", width: "49%" }}
              onChange={e => setAddress2(e.target.value)}
              value={address2}
              placeholder=""
            />
          </Form.Item>
        </div>
        <Button onClick={doSubmit}
          type="primary"
          style={{
            background: `${Common.color.f900}`,
            width: "50%",
            height: "60px",
            fontSize: "20px",
          }}></Button>
      </Form>
      <div style={{marginTop:"2rem"}}>
        <Link to='/mypage/signout' style={{fontSize:"2rem"}}></Link>
      </div>
    </div>
  );
};

export default MyMainPage;
